#input {
  width: 500px;
  height: 15px;
  display: flex;
  font-size: 20px;
  border-radius: 50px;
  padding: 20px;
  outline: none;
}

#input .active {
  border: 5px solid red;
}

.input {
  border: 5px solid var(--white);
}

.input.active {
  animation: test 2s;
}

@keyframes test {
  0% { border: 4px solid white; }
  50% { border: 4px solid red; }
  100% { border: 4px solid white; }
}
